.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 3rem 12% 3rem;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
}

.logo {
  font-size: 3rem;
  color: var(--text-color);
  font-weight: 800;
  cursor: pointer;
  transition: 0.3s ease;
}

.logo:hover {
  transform: scale(1.1);
}

.logo span {
  color: var(--main-color);
  text-shadow: 0 0 25px var(--main-color);
}

.navbar a{
  font-size: 1.8rem;
  color: var(--text-color);
  margin-left: 4rem;
  font-weight: 500;
  transition: 0.3s ease;
}

.navbar a:hover,
.navbar a.active {
  color: var(--main-color);
  border-bottom: 3px solid var(--main-color);
}

.navbar-blog {
  display: inline-block;
  padding: 0.7rem 2rem;
  background: var(--sec-bg-color);
  box-shadow: 0 0 15px var(--main-color);
  border-radius: 4rem;
  border: 2px solid var(--main-color);
  letter-spacing: 0.1rem;
  transition: 0.3s ease-in-out;
}

.navbar-blog:hover {
  transform: scale(1.05);
  border: 2px solid var(--main-color) !important;
}

#menu-icon {
  font-size: 3.6rem;
  color: var(--main-color);
  text-shadow: 0 0 25px var(--main-color);
  display: none;
}

@media(max-width:1024px) {
  #menu-icon {
    display: block;
  }
  .navbar {
    position: absolute;
    top: 100%;
    right: 5%;
    width: 50%;
    padding: 1rem 3rem;
    background-color: var(--sec-bg-color);
    border: 2px solid var(--main-color);
    border-radius: 2rem;
    z-index: 100;
    display: none;
  }
  .navbar a{
    display: block;
    font-size: 2rem;
    margin: 2rem 0;
    color: var(--text-color);
  }
  .navbar.active {
    display: block;
  }
}