.home {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15rem;
}

.home-content {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  text-align: left;
  justify-content: center;
  margin-top: 3rem;
}

.home-content h1 {
  font-size: 7rem;
  font-weight: 700;
  margin-top: 1.5rem;
  line-height: 1;
}

.home-content h1 span,
.home-content h3 span {
  color: var(--main-color);
  text-shadow: 0 0 25px var(--main-color);
}

.home-content h3 {
  margin-bottom: 2rem;
  margin-top: 1.2rem;
  font-size: 3.5rem;
  font-weight: 600;
}

.home-content p {
  font-size: 1.7rem;
  font-weight: 500;
  line-height: 1.8;
  max-width: 1000px;
}

.social-icons a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 4.5rem;
  height: 4.5rem;
  background: transparent;
  border: 2px solid var(--main-color);
  border-radius: 50%;
  color: var(--main-color);
  font-size: 2.5rem;
  margin: 3rem 1.5rem 3rem 0;
  transition: 0.3s ease-in-out;
}

.social-icons a:hover {
  color: var(--text-color);
  transform: scale(1.3)translateY(-5px);
  box-shadow: 0 0 25px var(--main-color);
  background-color: var(--main-color);
}

.button {
  display: inline-block;
  padding: 1rem 2.8rem;
  background: var(--main-color);
  box-shadow: 0 0 25px var(--main-color);
  border-radius: 4rem;
  font-size: 1.7rem;
  color: var(--bg-color);
  border: 2px solid transparent;
  letter-spacing: 0.1rem;
  font-weight: 600;
  transition: 0.3s ease-in-out;
}

.button:hover {
  transform: scale(1.05);
  box-shadow: 0 0 50px var(--main-color);
}

.button-group {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.button-group a:nth-of-type(2) {
  background-color: transparent;
  color: var(--main-color);
  border: 2px solid var(--main-color);
  box-shadow: 0 0 25px transparent;
}

.button-group a:nth-of-type(2):hover {
  background-color: var(--main-color);
  box-shadow: 0 0 50px var(--main-color);
  color: var(--bg-color);
}

.avatar .box {
  position: relative;
  top: 3rem;
  width: 30vw;
  height: 30vw;
  background: var(--bg-color);
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
  transition: 0.4s ease-in-out;
}

.avatar .box::before {
  content: '';
  position: absolute;
  inset: -10px 25%;
  background: var(--main-color);
  transition: 0.5s ease-in-out;
  animation: avatar-rotate 6s linear infinite;
}

.avatar .box:hover::before {
  inset: -20px 0;
}

@keyframes avatar-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.avatar .box::after {
  content: '';
  position: absolute;
  inset: 0.6rem;
  background: var(--bg-color);
  border-radius: 50%;
  z-index: 1;
}

.avatar .box div {
  position: absolute;
  inset: 3rem;
  border: 0.6rem solid var(--sec-bg-color);
  z-index: 3;
  border-radius: 50%;
  overflow: hidden;
}

.avatar .box img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media(max-width:1024px) {
  .home {
    flex-direction: column-reverse;
    gap: 3rem;
  }
  .home-content {
    align-items: center;
    text-align: center;
  }
  .avatar .box {
    width: 60vw;
    height: 60vw;
  }
  .home-content h1 {
    font-size: 4rem;
  }
  .home-content h3 {
    font-size: 2.4rem;
  }
  .home-content p {
    font-size: 1.5rem;
  }
}